@import url("https://fonts.googleapis.com/css?family=Bree+Serif&display=swap");

.Description {
  font-family: "Bree Serif", serif;
  font-size: 2rem;
  text-align: center;
  margin: 0;
  padding: 0;
  animation: fadeIn 3s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
